import Account from '../../common/Account.vue';
import Logs from '../../common/Logs.vue';

export default {
    components: { Account, Logs },
    props: {
        api_url: {
            type: String,
            default: '',
        },
        id: {
            type: String|Number,
            default: 0,
        },
        object_type: {
            type: String,
            default: ''
        },
        type: {
            type: String,
            default: 'facebook',
        },
        packageName: {
            type: String,
            default: ''
        },
        isComment: {
            type: Number,
            default: 0,
        },
        isBuffLive: {
            type: Number,
            default: 0,
        },
        typeMenu: {
            type: String,
            default: 'not_convert',
        }
    },
    data() {
        return {
            user: this.$store.state.user ?? {},
            packages: [],
            logOrders: [],
            loading: false,
            object_id: '',
            package_name: '',
            packageInfo: null,
            quantity: 100,
            limit: 1000,
            list_message: '',
            count: 0,
            numMinutes: 30,
            isDisable: false,
            isDisableQuantity: false,
            objectType: '',
            isShowFeeling: false,
        }
    },
    methods: {
        async getPackages() {
            try {
                let response = await this.$http.get(`/v2/get-package?id=${this.id}`)
                this.packages = response.data.data
                if (response.data.data.length > 0) {
                    this.packageInfo = response.data.data[0]
                    this.package_name = this.packageInfo.package_name
                }
            } catch (error) {
                this.$notify.error({
                    title: 'Error',
                    message: error.response.data.message
                });
            }
        },
        async logOrder() {
            try {
                let response = await this.$http.get(`/v2/log-order?type=${this.type}&m_id=${this.id}&package_name=&key=&limit=${this.limit}`)
                this.logOrders = response.data.data
            } catch (error) {
                this.$notify.error({
                    title: 'Error',
                    message: error.response.data.message
                });
            }
        },
        findPackage(packageItem) {
            this.packageInfo = packageItem
        },
        async buy() {
            try {
                let user = localStorage.getItem('user_info') ? JSON.parse(localStorage.getItem('user_info')) : null;
                if (user && user.api_key) {
                    this.loading = true
                    let params = {
                        object_id: this.object_id,
                        object_type: this.objectType,
                        package_name: this.package_name,
                        quantity: this.quantity,
                        list_message: this.list_message,
                        num_minutes: this.numMinutes,
                    }

                    await this.$http.post(this.api_url, params)
                    this.loading = false
                    this.$notify.success({
                        title: 'Success',
                        message: 'Tạo đơn thành công.'
                    });
                    this.logOrder()
                } else {
                    this.$notify.error({
                        title: 'Error',
                        message: 'Vui lòng đăng nhập để mua dịch vụ'
                    });

                    setTimeout(() => {
                        window.location.href = '/login'
                    }, 1000)
                }
            } catch (error) {
                this.loading = false
                this.$notify.error({
                    title: 'Error',
                    message: error.response.data.message
                });
            }
        },
        totalMoney() {
            let total = 0;
            if (this.isComment) {
                this.quantity = this.count
                this.isDisableQuantity = true
            }

            if (['facebook'].includes(this.type)) {
                if (['facebook_like_v3', 'facebook_like_v15', 'facebook_like_v7', 'facebook_like_v9', 'facebook_like_v2', 'facebook_like_v12', 'facebook_like_v16'].includes(this.package_name)
                    || ['/facebook-like-binh-luan/buy'].includes(this.api_url)
                ) {
                    this.isShowFeeling = true;
                } else {
                    this.isShowFeeling = false;
                }
                total = this.packageInfo.prices * this.quantity
            } else if (['facebook_eyes'].includes(this.type)) {
                if (['facebook_eyes_sv5','facebook_eyes_sv6','facebook_eyes_sv7','facebook_eyes_sv8','facebook_eyes_sv9','facebook_eyes_sv10','facebook_eyes_sv11','facebook_eyes_sv12','facebook_eyes_sv13'].includes(this.package_name)) {
                    this.numMinutes = 1;
                    this.isDisable = true;
                } else {
                    this.isDisable = false;
                    this.numMinutes = 30;
                }

                total = this.packageInfo.prices * this.quantity * this.numMinutes
                this.numMinutes = 30;
            } else if (['instagram', 'tiktok', 'shopee', 'youtube', 'telegram'].includes(this.type)) {
                total = this.packageInfo.prices * this.quantity
            }


            return new Intl.NumberFormat('de-DE').format(total)
        },
        async convertUid() {
            try {
                if (['facebook_eyes', 'view_story', 'not_convert'].includes(this.typeMenu)) {
                    return;
                }

                this.loading = true;
                let params = {
                    link: this.object_id,
                    type: this.typeMenu,
                }
                let res = await this.$http.post('/convert-uid', params)

                if (res.data == 0) {
                    this.$notify.error({
                        title: 'Error',
                        message: 'Thất bại, vui lòng thử lại hoặc nhập id thủ công'
                    });
                    this.object_id = ''
                } else {
                    this.object_id = res.data
                }
                
                this.loading = false
            } catch (error) {
                this.object_id = ''
                this.loading = false
                this.$notify.error({
                    title: 'Error',
                    message: error.response.data.message
                });
            }
        },
        confirmAlert() {
            let self = this
            let text = `Bạn sẽ tăng ${new Intl.NumberFormat('de-DE').format(this.quantity)} tương tác với giá ${this.packageInfo.prices}đ <br> Tổng tiền: ${this.totalMoney()} đ`
            Swal.fire({
                title: 'Hãy kiểm tra lại thông tin!',
                html: text,
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Mua',
                customClass: {
                    confirmButton: 'btn btn-primary me-1',
                    cancelButton: 'btn btn-outline-danger ml-1'
                },
                buttonsStyling: false
            }).then(function(result) {
                if (result.value) {
                    self.buy()
                }
            });
        }
    },
    watch: {
        list_message: function() {
            var contentsArr = this.list_message.split("\n")
            this.contentData = contentsArr.filter(item => item !== "")
            this.count = this.contentData.length
        },
        '$store.state.user': function() {
            this.user = this.$store.state.user
        },

    },
    mounted() {
        this.objectType = this.object_type
        this.getPackages()
        this.logOrder()
    }
}
